import { useWindowDimensions } from "../common";
import BannerImage from "../components/BannerImage";
import Footer from "../components/Footer";
import MenuBar from "../components/MenuBar";
import StickyHeader from "../components/StickyHeader";
import "../css/App.css";

function Terms() {
  const { width } = useWindowDimensions();
  return (
    <div className="page">
      {width > 768 ? <MenuBar transparent={true} /> : <StickyHeader />}
      {width > 768 ? (
        <BannerImage image={require("../images/banner-terms.jpg")} />
      ) : (
        <div style={{ position: "relative", paddingTop: 100 }}>
          <BannerImage image={require("../images/banner-terms.jpg")} />
        </div>
      )}
      <div
        className="container"
        style={{
          height: "auto",
          marginBottom: 64
        }}
      >
        <h2 style={{ marginTop: 24 }}>KUVAUSEHDOT</h2>
        <p>Varaamalla kuvauksen hyvksyt kuvausehdot.</p>
        <p>
          Niss sopimusehdoissa valokuvaajalla viitataan henkiln Sara
          Tuononen ja asiakkaalla valokuvauspalvelun tilaajaan.
        </p>
        <p style={{ fontWeight: "bold" }}>Sopimuksen hyvksyminen</p>
        <p>
          Asiakas perehtyy sopimusehtoihin ennen sovittua kuvausajankohtaa.
          Ostamalla valokuvaajan palveluita, asiakas sitoutuu noudattamaan nit
          sopimusehtoja. Mys valokuvaaja sitoutuu noudattamaan alla olevia
          ehtoja asiakkaansa kanssa.
        </p>
        <p style={{ fontWeight: "bold" }}>Varaukset</p>
        <p>
          Valokuvauksen varaus on sitova sitten, kun asiakas on maksanut
          kuvausmaksun ja valokuvaaja on lhettnyt asiakkaalle kirjallisen
          vahvistusviestin shkpostitse. Vahvistusviesti sislt tiedot
          yhdess asiakkaan kanssa sovitusta kuvauksen sisllst ja sen
          ajankohdasta. Kuvausmaksu tulee olla maksettuna ennen kuvausta.
          Maksamalla kuvausmaksun hyvksyt nm sopimusehdot.
        </p>
        <p style={{ fontWeight: "bold" }}>
          Koevedosgalleria ja valmiiden kuvien toimittaminen
        </p>
        <p>
          Kuvausten jlkeen kuvaaja kokoaa valitsemistaan onnistuneista
          otoksista katseltavaksi koevedosgallerian, josta asiakas valitsee
          haluamansa kuvapaketin. Asiakkaalla ei ole oikeutta kopioida tai
          ladata vedosgallerian kuvia omaan kyttn tai julkaista niit.
          Kuvaaja ky asiakkaan valitsemat kuvat lpi perusksittelyn
          valotuksen, valkotasapainon, kontrastin ja rajauksen osalta. Tarkempi
          kuvien ksittely ja kuvamanipulaatio (esimerkiksi ihon tasoittaminen
          tai esineiden poistaminen kuvasta) eivt automaattisesti kuulu
          palveluun ilman lismaksua. Koevedosgalleria toimitetaan asiakkaan
          nhtvksi viikon sisll kuvauksista. Asiakas on velvollinen
          valitsemaan itselleen kuvapaketin viikon sisll gallerialinkin
          vastaanotettuaan. Koevedosgalleria poistetaan asiakkaan valittua
          kuvat, ellei toisin sovita.<br></br>Valmiit kuvat toimitetaan
          asiakkaalle digitaalisessa muodossa linkin kautta tysikokoisina
          jpg.-tiedostoina. Raw-tiedostoja eli kuvien raakaversioita ei
          toimiteta. Ksiteltyjen kuvien toimitusaika on riippuvainen asiakkaan
          valitsemien kuvien mrst. Kun kuvapaketin maksu nkyy kuvaajan
          tilill, toimitetaan asiakkaalle valmiit kuvat 1-3 viikon sisll,
          ellei toisin sovita.
        </p>
        <p style={{ fontWeight: "bold" }}>Maksu</p>
        <p>
          Kuvausmaksu maksetaan aina ennen kuvausta, joka toimii ns.
          varausmaksuna. Lasku lhetetn asiakkaalle shkpostiin. Kun asiakas
          on kuvausten jlkeen valinnut ja maksanut haluamansa kuvapaketin,
          toimitetaan valmiit kuvat asiakkaalle. Mikli asiakas ei ole maksanut
          laskua erpivn menness, ei valokuvaajalla ole velvollisuutta
          toimittaa valmiita kuvia asiakkaalle. Kuvaaja ilmoittaa asiakkaalle
          tarjoamiensa kuvauspalveluiden kokonaishinnan sopimuksen laatimisen
          yhteydess. Mikli kuvaajalle aiheutuu yllttvi kuluja
          kuvaustilanteessa, joista ei ole aiemmin sovittu (esim. lauttamaksut,
          sisnpsymaksut tai parkkimaksut) vastaa asiakas nist
          ylimrisist kustannuksista.<br></br>Mikli kuvaus tapahtuu Turun
          seudulla, sisltyvt matkakustannukset kuvausmaksuun. Jos kuvaaja
          joutuu matkustamaan kuvauspaikalle yli 40km, tulee asiakkaan maksaa
          erikseen matkakuluista.
        </p>
        <p style={{ fontWeight: "bold" }}>
          Kuvaustyyli, laitteisto ja kuvien mahdollinen tuhoutuminen
        </p>
        <p>
          Asiakas on nhnyt kuvaajan aiempia tit, joten asiakas luottaa
          kuvaajan taitotasoon ja visioon kuvata. Asiakas ymmrt ja hyvksyy
          sen, ett kuvaajan kuvaustyyli voi muuttua ajan myt ja/tai
          olosuhteiden takia. Kuvaaja ei takaa, ett toimitettavat kuvat ovat
          samanlaisia/identtisi tyyliltn kuin hnen aikaisemmat kuvansa.
          Kuvaaja ptt kuvien taiteellisen tyylin vallitsevien olosuhteiden
          ja oman harkintansa mukaisesti. Esivalintagalleria sislt
          valokuvaajan harkinnan mukaan usein mys mustavalkokuvia. Jotkut kuvat
          on valittavina vain mustavalkoversioina tai vain vriversioina tai
          molempina. Suurin osa galleriastan kuvista on kuitenkin vrillisi.
          Eptodennkisess tilanteessa, jossa kuvat tuhoutuvat (esim.
          laiterikko), kuvaaja on velvollinen vahingonkorvauksiin palauttamalla
          korkeintaan asiakkaan siihen menness maksaman rahamrn takaisin.
          Kuvaaja on velvollinen tarjoamaan vastaavan maksuttoman kuvauksen
          asiakkaalle, mikli hnen kuvansa tuhoutuvat. Kuvaaja on velvollinen
          huolehtimaan kuvien huolellisesta varmuuskopioinnista.<br></br>
          Varmuuskopiointivelvollisuus pttyy 3kk kuvauksen jlkeen, eik
          kuvaajalla ole sen jlkeen en velvollisuutta silytt asiakkaan
          kuvia.
        </p>
        <p style={{ fontWeight: "bold" }}>
          Tekijnoikeus ja kuvien kyttoikeus
        </p>
        <p>
          Asiakkaalla on oikeus julkaista valokuvaajan ottamia valmiita kuvia
          omilla sosiaalisen median alustoillaan sek teett valokuvista
          kopioita yksityiseen sek lhipiirins kyttn.<br></br>Asiakkaalla
          ei ole oikeutta myyd tai luovuttaa kuvia kolmannelle osapuolelle
          ilman valokuvaajan lupaa. Asiakkaalla ei ole lupaa muokata tai rajata
          kuvia ilman kuvaajan lupaa. Asiakkaan julkaistessa valokuvaajan
          ottamia kuvia, tulee valokuvaajan nimi Sara Tuononen Photography
          (instagramissa:{" "}
          <a
            href="https://instagram.com/saratuononen_photography"
            target="_blank"
            rel="noreferrer"
          >
            @saratuononen_photography
          </a>
          ) merkit julkaisuun.
        </p>
        <p>
          Kuvaajalla on yksinomainen oikeus omiin kuviinsa, niiden kyttn,
          julkaisemiseen ja myymiseen. Kuvaaja voi julkaista kuviaan vapaasti
          sosiaalisessa mediassa, ellei toisin sovita. Asiakkaalla on oikeus
          pyyt, ettei hnest otettuja tunnistettavia kuvia julkaista tai
          kytet julkisesti, jolloin kuvaaja kunnioittaa tt toivetta.
        </p>
        <p style={{ fontWeight: "bold" }}>
          Erimielisyydet ja sopimuksen purkaminen
        </p>
        <p>
          Mikli asiakkuuden aikana her erimielisyyksi, kyseisist asioista
          neuvotellaan ensisijaisesti asiakkaan ja valokuvaajan vlill
          rakentavalla ja kaikkia osapuolia kunnioittavalla tavalla.<br></br>
          Asiakkaalla on oikeus perua kuvaus 24h ennen kuvausta. Mikli asiakas
          peruu kuvauksen alle 24h ennen kuvausta, ei kuvaajalla ole
          velvollisuutta palauttaa kuvausmaksua. Juhlien ja tapahtumien
          varausmaksu on 30% kokonaissummasta, jota ei palauteta, vaikka asiakas
          peruisi kuvauksen. Mys valokuvaajalla on oikeus purkaa sopimus,
          jolloin hnen tulee antaa asiakkaalle selitys sopimuksen purkamiseen
          johtaneista syist sek palauttaa mahdolliset varausmaksut.<br></br>
          Valokuvaaja ei ole velvoitettu maksamaan asiakkaalle ylimrisi
          vahingonkorvauksia (varausmaksujen palauttamisen lisksi), jos hn ei
          ylitsepsemttmn esteen (esimerkiksi sairastumisen,
          loukkaantumisen, kuoleman tai muun ennakoimattoman tilanteen) vuoksi
          kykene fyysisesti saapumaan kuvauspaikalle sovittuna ajankohtana.
          Mikli kuvaustilanteen ainutkertaisuuden vuoksi sovittua
          kuvausajankohtaa on mahdoton siirt (esim. ht, hautajaiset tms
          tapahtumat), on kuvaajalla velvollisuus yritt jrjest paikalle
          toinen kuvaaja tilalle. Kuvaaja ei ole kuitenkaan vastuussa siit, jos
          sijaista ei hnen toimestaan lydy.<br></br>Kuvaustilanteessa
          kuvaajalla on oikeus keskeytt kuvaustilanne, mikli joku
          kuvausseurueesta kyttytyy uhkaavasti tai vkivaltaisesti joko
          kuvaajaa tai kalustoa kohtaan.<br></br>Asiakas takaa valokuvaajalle
          tyrauhan ja toimii tmn kanssa yhteistyss sek ennen kuvauksia
          ett kuvausten aikana, jotta valokuvaukset onnistuvat odotetulla
          tavalla.
        </p>
        <p>
          Thn sopimukseen sovelletaan Suomen lakia. Sopimuksesta aiheutuvat
          erimielisyydet ratkaistaan ensisijaisesti sovittelemalla. Viime
          kdess erimielisyydet ratkaistaan Helsingin krjoikeudessa.
        </p>
        <p> Sara Tuononen Photography</p>
      </div>
      <Footer />
    </div>
  );
}

export default Terms;
