import { useWindowDimensions } from "../common";
import BannerImage from "../components/BannerImage";
import Feedback from "../components/Feedback";
import Footer from "../components/Footer";
import MenuBar from "../components/MenuBar";
import StickyHeader from "../components/StickyHeader";
import "../css/App.css";

function About() {
  const { width } = useWindowDimensions();
  return (
    <div className="page">
      {width > 768 ? <MenuBar transparent={true} /> : <StickyHeader />}
      {width > 768 ? (
        <BannerImage image={require("../images/banner-about.jpg")} />
      ) : (
        <div style={{ position: "relative", paddingTop: 100 }}>
          <BannerImage image={require("../images/banner-about.jpg")} />
        </div>
      )}
      <div
        className="container"
        style={{
          height: "auto",
          marginBottom: 64
        }}
      >
        <div style={{ display: "flex", flexWrap: "wrap" }}>
          <div
            style={
              width > 1008
                ? {
                    width: "50%",
                    height: "auto"
                  }
                : {
                    width: "100%",
                    height: "auto"
                  }
            }
          >
            <div
              style={
                width > 1008
                  ? {
                      paddingRight: 24,
                      paddingLeft: 24
                    }
                  : {
                      paddingRight: 24,
                      paddingLeft: 24
                    }
              }
            >
              <h2>KUKA OLEN?</h2>
              <p
                style={
                  width > 1008
                    ? {
                        textAlign: "justify"
                      }
                    : {}
                }
              >
                Ihanaa kun lysit sivuilleni! Sara Tuononen Photographyn takaa
                lytyy helposti kaikesta innostuva, empaattinen sek tylleen
                omistautunut kuvailija. Olen jo vuosia tyskennellyt
                opetusalalla lasten parissa, jonka koen antaneen minulle vahvat
                vuorovaikutustaidot. Kuvaamisessa parasta on tavata erilaisia
                persoonia ja pst taltioimaan heidn tarinansa kuvan keinoin.
                Taiteen ja itseilmaisun eri muodot ovat olleet vahvasti osa
                elmni lapsesta saakka. Kuvaaminen tempaisi mukaansa jo
                teinin kavereita kuvaillessa. Palo roihahti yh enemmn, kun
                huomasin miten iso merkitys kuvilla voi toiselle olla. Jos
                pystyn kohottamaan jonkun itsetuntoa, tai auttamaan nkemn
                itsens lempemmin, on se sanoinkuvaamattoman palkitsevaa.
              </p>
              <p
                style={
                  width > 1008
                    ? {
                        textAlign: "justify"
                      }
                    : {}
                }
              >
                Mit enemmn vuosia on vierinyt, olen alkanut paremmin
                ymmrtmn ohikiitvien hetkien arvon, ja sen kuinka kuviin
                investoimisesta kiitt itsen myhemmin. Lopulta
                harrastuksesta alkoi muotoutua bisnes, ja yritykseni perustin
                kevll 2021. Sen jlkeen olen pssyt kuvailemaan vhn
                kaikkea laidasta laitaan. Erityisen lhell sydntni on
                erilaisten tunteiden ja tunnelmien taltioiminen kuviin.
              </p>
              <p
                style={
                  width > 1008
                    ? {
                        textAlign: "justify"
                      }
                    : {}
                }
              >
                Kuvaajana olen lempe ja helposti lhestyttv, eik kuvauksia
                tarvitse yhtn jnnitt. Ei tarvitse tiet, miten kameran
                edess ollaan, min autan ja ohjaan. Yritykseni arvoja ohjaavat
                luotettavuus, yhdenvertaisuus ja turvallisuus. Mikli tyylini
                puhuttelee sinua, ota rohkeasti yhteytt niin suunnitellaan
                kuvaus juuri sinun tarpeillesi!
              </p>
            </div>
          </div>
          <div
            style={
              width > 1008
                ? {
                    width: "50%",
                    height: "auto",
                    display: "flex",
                    alignItems: "center"
                  }
                : {
                    width: "100%",
                    height: "auto",
                    display: "flex",
                    alignItems: "center"
                  }
            }
          >
            <div style={{ display: "flex" }}>
              <img
                style={{
                  width: "90%",
                  marginLeft: "auto",
                  marginRight: "auto"
                }}
                src={require("../images/sara.jpg")}
                alt="Sara Tuononen Valokuvaaja"
              />
            </div>
          </div>
        </div>

        <h2 style={{ marginTop: 34 }}>ASIAKKAIDEN KOKEMUKSIA</h2>
        <div
          style={{
            display: "flex",
            flexWrap: "wrap"
          }}
        >
          <Feedback
            feedback="Etsimme kuvaajaa, jonka nkemys ja tyyli kohtaisi omamme ja
              lysimme Saran instagramin kautta. Hnen kanssa oli enemmn kuin
              miellyttv asioida, vastaukset saimme kysymyksiin nopeasti ja
              ideoita lytyi. Itse h-hetken valokuvaus hoitui sulavasti ja
              vahvalla ammattitaidolla. Valmiit kuvat saimme nopeasti ja ne
              olivat tynn raikkautta ja luonnollisuutta - juuri sit mit
              kuvilta toivoimme! Lmpimsti voin suositella Saraa kuvaamaan omat
              trket hetkesi. Uusia kuvauksia odottaen."
            name="Sanni"
          />
          <Feedback
            feedback="Saran kuvattavana kamera unohtui. Koin kuvaushetken
            kiireettmn, vapauttavana, rentona ja miellyttvn,
            hauskanakin! Oma epvarmuus ja vaivautuneisuus ajatuksesta olla
            kuvassa haihtui. Kokemus oli itselleni terapeuttinen, ensi kertaa
            katson omaa kuvaa hymyillen, itsevarmempana ja itsestni piten ja
            iloiten! Kiitos Sara!"
            name="Tiina"
          />
          <Feedback
            feedback="The pictures are absolutely stunning! I love love love love how
            you captured so well the essence of our kids. I'm completely in
            love with your pictures and I can't wait to have a new photoshoot
            with you. I must say once more that you are super talented Sara!
            You have the charisma and the eye for photography. I was planning
            on having like 5 pictures from this session, but I have picked 38!
            And only after having a through selection process."
            name="Vanessa"
          />
          <Feedback
            feedback="Sun kanssa kuvien ottaminen tuntu tosi luonnolliselta eik
            tarvinnu jnnitt yhtn! Olit tosi hyv mys ideoimaan, millasia
            kuvia otetaan."
            name="Rebecca"
          />
          <Feedback
            feedback="Pariskuntakuvauksessa meill oli tosi iloinen ja rento tunnelma,
            ei tarvinnut jnnitt yhtn. Saatiin kauniit kuvat ja ihanat
            muistot."
            name="Anita"
          />

          <Feedback
            feedback="Kuvausjnnitys laukesi heti kun psi Saran kameran taakse, kuvattavana oleminen alkoikin tuntua luontevalta ja mukavalta."
            name="Susanna"
          />

          <Feedback
            feedback="Saran kanssa kuvailu oli tosi luonnollista ja teki itselle rennon fiiliksen, joka tallentui mys kuviin. Tuli olo pyyt Saraa uudelleen kuvaamaan. Laadukkaiden kuvien lisksi kuvien ksittely sai kuvat nyttmn tosi ammattimaisilta."
            name="Kehonhuolto Anna Savolainen"
          />
          <Feedback
            feedback="Sara on aivan mahtava valokuvaaja ja hnen seurassaan pystyy rentoutumaan aivan tysin, jopa tllainen kameraa karkaileva ihminen. Osaa huomioida toiveet, mutta oma visuaalinen nkemys ei myskn puutu. Saat satavarmasti onnistuneita kuvia."
            name="CAMA sportswear"
          />

          <Feedback
            feedback="Saran kanssa newborn-kuvaus sujui rauhallisissa merkeiss, vauvantahtisesti. Saralla oli paljon omia, ihania ideoita kuvien suhteen, mutta hn kuunteli mys meidn nkemyksimme ja toiveitamme. Kuvista tuli aivan valtavan ihania! Suosittelen Saraa todella lmpimsti!"
            name="Mimmi"
          />
          <Feedback
            feedback="Olen ollut Saran kuvattavana paljon erilaisissa kuvausympristiss ja kerta toisensa jlkeen kuvat ovat olleet upeita. Saran edess kuvattavana on helppoa ja rentoa olla. Hn osaa hyvin kytt omaa taiteellista silmns ja luoda kauniita kuvia."
            name="Emilia"
          />
          <Feedback
            feedback="Saran kanssa kuvaus sujui erittin luontevasti! Hn oli samalla huomaamaton, mutta osaa mys ammattimaisesti ohjata malliaan. Tunnelma oli rento, eik aikataululla luoda painetta. Annan ison suosituksen hnelle. Hevoskuvaus sujui todella hyvin ja sain toteutetuksi toivomani."
            name="Nana"
          />
          <Feedback
            feedback="Ollaan iloisia miten hyvi kuvia saatiin! Puhuttiin ett tst pit kyll tehd sellanen vhintn kerran vuodessa juttu! Niin parasta kun saa oikeesti hyvi kuvia talteen."
            name="Oona"
          />
          <Feedback
            feedback="Boudoir-kuvaukset tuntuivat etukteen jnnittvlt ja hieman vaivaannuttavaltakin ajatukselta, mutta Saran valokuvattavana oma epvarmuus vistyi hetkess ja tilalle tuli hyvin itsevarma ja luonteva olo kameran edess. Kuvaussessio oli hyvin hauska ja voimaannuttava kokemus, jonka aikana tunnelma oli hyvin rento ja vapautunut. Saran ottamista kuvista tuli lopulta todella upeita, jotka kohottivat mys omaa itsetuntoa ja saivat todella arvostamaan omaa rakasta kehoa!"
            name="Heidi"
          />
          <Feedback
            feedback="Aivan mahtavaa! Kuvista tuli kyll niin upeita, kiitos sulle. Ihmeess otan yhteytt jatkossakin, jos tulee mieleen, mit kuvata. Suosittelen mys tutuille, jos etsivt kuvaajaa."
            name="Katri"
          />
          <Feedback
            feedback="Kuvauksessa oli hyvin vapautunut ja positiivinen tunnelma. Ihanaa kun kuvaaja oli itse mys hyvin innostunut ja aikataulukin joustava. Lopputulos aivan priimaa. Kaikki toimi niin kuin sovittiin. Lmmin suositus!"
            name="Nina"
          />
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default About;
