import { useWindowDimensions } from "../common";
import BannerImage from "../components/BannerImage";
import FaqItem from "../components/FaqItem";
import Footer from "../components/Footer";
import MenuBar from "../components/MenuBar";
import StickyHeader from "../components/StickyHeader";
import "../css/App.css";

function Faq() {
  const { width } = useWindowDimensions();
  return (
    <div className="page">
      {width > 768 ? <MenuBar transparent={true} /> : <StickyHeader />}
      {width > 768 ? (
        <BannerImage image={require("../images/newborn/newborn16.jpg")} />
      ) : (
        <div style={{ position: "relative", paddingTop: 100 }}>
          <BannerImage image={require("../images/newborn/newborn16.jpg")} />
        </div>
      )}
      <div
        className="container"
        style={{
          height: "auto",
          marginBottom: 64
        }}
      >
        <h2 style={{ marginTop: 24 }}>USEIN KYSYTTY</h2>
        <ul style={{ listStyleType: "none", padding: 0 }}>
          <li>
            <FaqItem
              title="Milloin kuvaus maksetaan?"
              content="Lhetn kuvausmaksun asiakkaalle heti varauksen yhteydess. Kuvausmaksu tulee olla maksettuna ennen kuvauksia. Kuvapaketin lasku lhetetn esivalinnan jlkeen."
            />
          </li>
          <li>
            <FaqItem
              title="Mit tarkoittaa esivalintagalleria ja kauanko kuvien saaminen kest?"
              content="Esivalintagalleria koostuu vhintn n. 40-60 kuvaajan valitsemasta helmest, eli kuvausten parhaista otoksista. Kuvausten jlkeen viikon sisll kuvaaja toimittaa sinulle shkisen linkin, jonka kautta pset katsomaan nit kuvia ja valitsemaan haluamasi kuvamrn. Sinulla on linkin vastaanotettuasi viikko aikaa valita haluamasi kuvapaketti.  Kuvaaja ksittelee valitsemasi kuvat valmiiseen muotoonsa kolmen viikon sisll loppulaskun nkyess tilill ja toimittaa ne sinulle shkisell linkill jpg.-muodossa."
            />
          </li>
          <li>
            <FaqItem
              title="Saanko itse muokata kuvia?"
              content="Et. Kuvien tekijnoikeus silyy kuvaajalla, joka tarkoittaa mys sit, ett kuvien jatkotystminen ja rajailu ei ole sallittua. Kuvat edustavat kuvaajan tyyli ja kdenjlke, eik niit siksi voi lhte muuttamaan. Huomioithan siis kuvaajaa valitsessasi, ett hnen tyyli miellytt sinua."
            />
          </li>
          <li>
            <FaqItem
              title="Miss kuvaukset tapahtuvat?"
              content="Kuvauslokaatio sovitaan asiakkaan kanssa yhdess. Kuvaukset voivat tapahtua mahdollisuuksien mukaan esimerkiksi ulkomiljss, asiakkaan kotona, kuvaajan kotona, hotellissa, you name it! Ideoidaan yhdess. Mys studion vuokraamisesta kuvauksia varten voidaan keskustella asiakkaan kanssa, mikli sellaiselle on tarvetta. 
              Kuvaukset tapahtuvat lhtkohtaisesti Turun seudulla, ellei toisin sovita."
            />
          </li>
          <li>
            <FaqItem
              title="Kuvaukset jnnitt. Pitk osata olla kameran edess? "
              content="Ei tarvitse todellakaan tiet miten kameran edess ollaan! Iso osa kuvattavistani ovat ensikertalaisia. Lhes kaikkia jnnitt jossain mrin kuvauksiin tuleminen ja se on tysin luonnollista. Lupaan neuvoa, ohjata ja olla lempe. Jutskaillaan, hengaillaan ja otetaan samalla kuvia. Kameran edess ei tarvitse pntt, poseerata tai edes hymyill jos ei silt tunnu. Yhdess mietitn, miten saadaan tuntemaan olosi voimaantuneeksi ja upeaksi, jotta saadaan juuri sinun nkiset kuvat!"
            />
          </li>
          <li>
            <FaqItem
              title="Miten kannattaa pukeutua kuvauksiin?"
              content="Kuvauksissa trkeint on pukea juuri sellaiset vaatteet, mitk saavat olosi itsevarmaksi ja hyvksi. 
              Yleisin vinkkein suosittelen maanlheisi, vaaleita ja neutraaleja vrej, sill ne toimivat aina. Printtej tai kuviollisia vaatteita en suosittele, sill ne vetvt usein huomion liikaa puoleensa. Jos tulet vaikkapa kumppanin tai perheen kanssa kuvauksiin, kannattaa mietti vaatteiden ja vrien yhteensointuvuutta. Liehuvat helmat, hengittvt ja vljt kankaat ja erilaiset tekstuurit vaikkapa farkuissa tai muhkeissa villapaidoissa nyttvt kuvissa hyvlt. Yksinkertaisuus toimii aina hyvin. Mikli kaipaat apua asuvalinnassa, voit aina ottaa minuun yhteytt asiaan liittyen, niin voimme yhdess ideoida. Nm ovat kuitenkin vain vinkkej, eli itse saat tietenkin ptt mik miellytt sinua eniten.
              "
            />
          </li>
          <li>
            <FaqItem
              title="Voiko kuvauksissa olla toinen asukokonaisuus mukana?"
              content="Tottakai! Usein asiakkaat haluavat ottaa vaihtovaatteet mukaan, jotta saadaan erilaisia kuvia."
            />
          </li>
          <li>
            <FaqItem
              title="Haaveena on kuvauttaa lapsi, mutta hn on luonteeltaan vilkas eik pysy paikallaan poseeraamassa. Voidaanko silti tulla kuvauksiin?"
              content="Ehdottomasti! Lapsi on terve kun se leikkii ja touhuaa. Olen ammatiltani varhaiskasvatuksen opettaja, joten lasten kanssa leikkiminen ja vuorovaikutus on minulle tuttua. Tulkaa rennoin mielin ja asentein, niin hyv tulee. Lapsi aistii helposti, jos vanhemmat ovat stressaantuneita ja yrittvt vkisin saada lapsen hymyilemn. Lasten kanssa kuvaaminen on pelkn poseeraamisen sijaan usein tilanteisiin heittytymist ja hauskan pitoa. Lhdetn yhdess vaikka rannalle juoksemaan tai metsretkelle tutkimaan luontoa. Samalla voidaan rpsi kuvia ja saada aitoja hetki ikuistettua."
            />
          </li>
          <li>
            <FaqItem
              title="Pitk boudoir-kuvauksissa olla alusvaatteissa ja nytt paljasta pintaa?"
              content="Ei tarvitse jos ei halua. Sin ptt omat rajasi ja sen mik tuntuu juuri sinusta hyvlt. Sensuelli tunnelma ei edellyt paljasta pintaa. Toiselle boudoir-kuvaus tarkoittaa vaikkapa paljaan olkapn nyttmist, kun taas toinen kokee olonsa kauniiksi alusvaatteillaan. Trkeint on, ett sinulla on hyv olla. Kannustan kuitenkin laittamaan omat lempparialusvaatteet itsevarmuutta tuomaan, ja myskin silt varalta jos kuvauksissa tuleekin yhtkki tosi itsevarma fiilis!"
            />
          </li>
          <li>
            <FaqItem
              title="Kuinka paljon aikaisemmin kuvaukset kannattaa varata?"
              content="Heti kun tiedt, ett sinulla on trke elmntilanne tulossa, josta haluat kuvia. Joskus kalenterini on jo pidemmlle buukattuna, mutta aina kannattaa kysy lyhyellkin varoitusajalla. Joskus voi lyty peruutusaikoja tai mahdollisuus tehd kalenteriin viel tilaa."
            />
          </li>
          <li>
            <FaqItem
              title="Milloin on hyv aika odotuskuvauksille? Ent vastasyntyneen kuvaukselle?"
              content="Usein suositellaan, ett odotuskuvat otettaisiin raskausviikoilla 30-36. Tllin vatsa alkaa olla suloisen pyre, eik olo viel vlttmtt niin tukala kuin viimeisill viikoilla. Thnkn ei ole kuitenkaan oikeaa tai vr vastausta, eli voit varata odotuskuvauksen juuri silloin kun itse sen koet parhaaksi. Ihan loppuun ei kannata kuvausta jtt, jottei ky niin ett vauva ehtiikin synty ennen kuvausta.
              Vastasyntyneen kuvaus toteutetaan kahden viikon sisll vauvan syntymst."
            />
          </li>
          <li>
            <FaqItem
              title="Onko asiakkaana pakko antaa suostumus kuvien julkiseen kyttn esimerkiksi sosiaaliseen mediaan tai muuhun markkinointiin?"
              content="Ei ole. Kysyn aina erikseen luvat kuvien kyttn. Kaikki eivt esimerkiksi halua lapsiensa nkyvn sosiaalisessa mediassa, mink ymmrrn. Toki olen aina iloinen, jos saan tydent portfoliotani asiakkaiden kuvilla, mutta se ei ole vaatimus."
            />
          </li>
        </ul>
      </div>
      <Footer />
    </div>
  );
}

export default Faq;
